  display: flex;
}

.card__img {
  width: 160px;
  height: 134px;
  margin: 0 auto;
  display: block;
}

.card__title {
  color: #060b35;
  margin-top: 40px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__subtitle {
  color: #616070;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
}

.card__review-group {
